:root {
  --background: #ffffff;
  --foreground: #171717;
  --primary-color: #006475;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  scroll-behavior: smooth;
}
.primary-text-color {
  color: var(--primary-color);
}
.primary-background-color {
  background: var(--primary-color);
}
.scrollbar-hide {
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}
@keyframes fadeSlideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-2.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.fade-slide-in-top {
  animation: fadeSlideInFromTop 0.7s ease-in-out forwards;
}
@keyframes fadeSlideInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(2.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.fade-slide-in-bottom {
  animation: fadeSlideInFromBottom 0.7s ease-in-out forwards;
}
@keyframes fadeSlideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-2.5rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.fade-slide-in-left {
  animation: fadeSlideInFromLeft 0.7s ease-in-out forwards;
}
@keyframes shakePause {
  0%,
  10% {
    transform: translateY(0);
  }
  15% {
    transform: translateY(-4px);
  }
  20% {
    transform: translateY(4px);
  }
  25% {
    transform: translateY(-4px);
  }
  30% {
    transform: translateY(4px);
  }
  35%,
  100% {
    transform: translateY(0);
  }
}
.shake-pause {
  animation: shakePause 3s ease-in-out infinite;
}
@keyframes smoothShake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-4px);
  }
  50% {
    transform: translateX(4px);
  }
  75% {
    transform: translateX(-4px);
  }
  100% {
    transform: translateX(0);
  }
}
.smooth-shake {
  animation: smoothShake 0.6s ease-in-out;
}
.smooth-shake-infinite {
  animation: smoothShake 0.6s ease-in-out infinite;
}
@keyframes fade-in {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fade-in {
  animation: fade-in 0.6s ease-out forwards;
}
